import Vue from 'vue'
import VueRouter from 'vue-router'
// import Home from '../views/Home.vue'
import Selectone from '../views/Selectone.vue'
import Login from '../views/Login.vue'
import Teacher from '../views/Teacher.vue'
import Question from '../views/Question.vue'
import Task from '../views/Task.vue'
import Tasks from '../views/Tasks.vue'
import Testbench from '../views/Testbench.vue'

Vue.use(VueRouter)

const routes = [{
		path: '/',
		name: 'Login',
		component: Login
	},
	{
		path: '/come',
		name: 'Selectone',
		component: Selectone
	},
	{
		path: '/Teacher',
		name: 'Teacher',
		component: Teacher
	},
	{
		path: '/Question',
		name: 'Question',
		component: Question
	},
	{
		path: '/Task',
		name: 'Task',
		component: Task
	},
	{
		path: '/Tasks',
		name: 'Tasks',
		component: Tasks
	},
	{
		path: '/Testbench',
		name: 'Testbench',
		component: Testbench
	}
]

const router = new VueRouter({
	routes
})

export default router
