                    <el-menu-item index="1">
                        <i class="el-icon-document"></i>
                        <span slot="title">
                            <router-link to="/Teacher">
                                管理题目
                            </router-link>
                        </span>
                        <router-view></router-view>
                    </el-menu-item>
                    <el-menu-item index="2">
                        <i class="el-icon-edit"></i>
                        <span slot="title">
                            <router-link to="/Task">
                                新建题目
                            </router-link>
                        </span>
                        <router-view></router-view>
                    </el-menu-item>
                    <el-menu-item index="3">
                        <i class="el-icon-notebook-2"></i>
                        <span slot="title">
                            <router-link to="/Tasks">
                                作业管理
                            </router-link>
                        </span>
                        <router-view></router-view>
                    </el-menu-item>
                    <el-menu-item index="4">
                        <i class="el-icon-edit-outline"></i>
                        <span slot="title">
                            <router-link to="/Question">
                                新建作业
                            </router-link>
                        </span>
                        <router-view></router-view>
                    </el-menu-item>
                </el-menu>
            </el-col>
            <el-col :span="18">
                <div class="workTitle">
                    题库
                </div>
                <div>
                    <router-link to="/Task">
                        <el-button type="primary">新建题目</el-button>
                    </router-link>
                    <router-view></router-view>
                </div>
                <div class="tableBox">
                    <el-table :data="taskData" style="width: 100%">
                        <el-table-column label="题目名称" width="180">
                            <template slot-scope="scope">
                                <span style="margin-left: 10px">{{ scope.row.tname }}</span>
                            </template>
                        </el-table-column>
                        <el-table-column label="题目描述" width="180" show-overflow-tooltip>
                            <template slot-scope="scope">
                                <span style="margin-left: 10px">{{ scope.row.tdis }}</span>
                            </template>
                        </el-table-column>
                        <el-table-column label="题目类别" width="180">
                            <template slot-scope="scope">
                                <span style="margin-left: 10px">{{ scope.row.ttype }}</span>
                            </template>
                        </el-table-column>
                        <el-table-column label="操作">
                            <template slot-scope="scope">
                                <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                                <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
