                            <template slot-scope="scope">
                                <span style="margin-left: 10px">{{ scope.row.qname }}</span>
                            </template>
                        </el-table-column>
                        <el-table-column label="作业描述" width="180">
                            <template slot-scope="scope">
                                <span style="margin-left: 10px">{{ scope.row.qdis }}</span>
                            </template>
                        </el-table-column>
                        <el-table-column label="截止日期" width="280">
                            <template slot-scope="scope">
                                <span style="margin-left: 10px">{{ scope.row.enddate }}</span>
                            </template>
                        </el-table-column>
                        <el-table-column label="操作">
                            <template slot-scope="scope">
                                <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                                <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
