<script>
    import axios from 'axios'

    export default {
        name: 'Tasks',
        data() {
            return {
                tableData: [],
                taskData: [],
            }
        },
        methods: {
            handleOpen(key, keyPath) {
                console.log(key, keyPath);
            },
            handleClose(key, keyPath) {
                console.log(key, keyPath);
            },
            handleEdit(index, row) {
                console.log(index, row);
            },
            handleDelete(index, row) {
                console.log(index, row);
            }
        },
        mounted() {
            axios
                .post('http://123.56.0.67:9500/api/teacher/getQuestions')
                .then(response => (
                    console.log(this.tableData = response.data.object)))
                .catch(function(error) { // 请求失败处理
                    console.log(error);
                });
            axios
                .post('http://123.56.0.67:9500/api/teacher/getTasks')
                .then(response => (
                    console.log(this.taskData = response.data.object)))
                .catch(function(error) { // 请求失败处理
                    console.log(error);
                });
        }
    }
</script>

<style scoped="scoped">
    .tac {
        overflow: hidden;
    }

    .el-menu-vertical-demo {
        height: 100vh;
    }

    .workTitle {
        float: left;
        font-size: 40px;
        font-weight: bold;
        margin: 20px;
        width: 160px;
        text-align: left;
    }

    .tableBox {
        margin: 40px;
    }

    a {
        text-decoration: none;
        color: #FFFFFF;
    }

    .el-button--primary {
        margin-top: 36px;
        margin-right: 110px;
    }

    .exit {
        position: absolute;
        right: 10px;
        top: 10px;
    }
</style>
