<template>
    <div>
        <div class="content">
            <div class="teacherTitle">
                <router-link to="/Teacher">教师登录</router-link>
                <router-view></router-view><strong></strong>
            </div>
            <div class="title">登录</div>
            <img src="../assets/logo.png">
            <div class="formBox">
                <el-form :model="ruleForm" status-icon ref="ruleForm" label-width="80px" class="demo-ruleForm">
                    <el-form-item label="用户名">
                        <el-input v-model="ruleForm.name"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" prop="pass">
                        <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
                    </el-form-item>

                    <el-form-item>
                        <el-button type="primary" @click="submitForm()">登录</el-button>
                    </el-form-item>
                </el-form>
            </div>

        </div>
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: 'Login',
        data() {
            return {
                ruleForm: {
                    name: '',
                    pass: '',
                },

            };
        },
        methods: {
            submitForm() {
                axios
                    .post('http://123.56.0.67:9500/api/signin/', {
                        "username": this.ruleForm.name,
                        "upasswd": this.ruleForm.pass
                    })
                    .then(response => (
                        console.log(response),this.$router.push({
                            path: '/Testbench'
                        })
                    ))
                    .catch(function(error) { // 请求失败处理
                        console.log(error);
                        alert("帐号或者密码错误！")
                    });
            },
        },
        mounted() {

        }
    }
</script>

<style>
    body {
        margin: 0;
    }
    .content {
        width: 800px;
        margin: 60px auto;
        padding: 20px;
        border: 2px solid grey;
        border-radius: 4px;
    }

    .teacherTitle {
        float: right;
        cursor: pointer;
    }

    .title {
        font-size: 36px;
        font-weight: bold;
        margin: 32px;
    }

    .el-form-item__label {
        text-align: left;
    }

    .formBox {
        width: 400px;
        margin: auto;
        padding-right: 40px;
    }

    .teacherTitle a {
        color: #409EFF;
        text-decoration: none;
        font-weight: bold;
        font-size: 20px;
    }
</style>
